import React from "react";
import moment from "moment";

import {
  Typography,
  AppBar,
  Toolbar,
  IconButton,
  Drawer,
  MenuItem,
  makeStyles,
} from "@material-ui/core";
import SettingsIcon from "@material-ui/icons/Settings";
import MenuIcon from "@material-ui/icons/Menu";
import { DatePicker } from "@material-ui/pickers";

// Styles
const useStyles = makeStyles((theme) => ({
  title: {
    flexGrow: 1,
  },
  navBar: {
    "& li": {
      width: "250px",
    },
  },
  padding10: {
    "& label": {
      padding: "10px",
    },
    padding: "10px",
  },
}));

export default function Header(props) {
  const [navBarOpen, setNavbarOpen] = React.useState(false);

  const selectedDate = props.selectedDate,
    setSelectedDate = props.setSelectedDate;

  const classes = useStyles();

  const handleDateChange = (date) => {
    const localStorageObj = JSON.parse(
      localStorage.getItem(moment(date).format("DDMMYYYY"))
    );
    if (localStorageObj) {
      props.setGratitudeList(localStorageObj.gratitudeList);
      props.setMotherCare(localStorageObj.motherCare);
      props.setTodayFeeling(localStorageObj.todayFeeling);
      props.setMeditationCheck(localStorageObj.meditationCheck);
      props.setExerciseCheck(localStorageObj.exerciseCheck);
      props.setYearGoalsList(localStorageObj.yearGoalsList);
      props.setYearTopGoalsList(localStorageObj.yearTopGoalsList);
      props.setMonthGoalsList(localStorageObj.monthGoalsList);
      props.setWeekGoalsList(localStorageObj.weekGoalsList);
      props.setTodoList(localStorageObj.todoList);
      props.setLegMassage(localStorageObj.legMassage);
      props.setMotherExercise(localStorageObj.motherExercise);
      props.setSunScreenMorCheck(localStorageObj.sunScreenMorCheck);
      props.setSunScreenEveCheck(localStorageObj.sunScreenEveCheck);
      props.setRawVegIntakeCheck(localStorageObj.rawVegIntakeCheck);
      props.setWaterCheck(localStorageObj.waterCheck);
      props.setRetinaCheck(localStorageObj.retinaCheck);
      props.setSunScreenEveCheck(localStorageObj.sunScreenEveCheck);
      props.setSoakDhaniya(localStorageObj.soakDhaniya);
      props.setNiceToday(localStorageObj.niceToday);
      props.setRateUrself(localStorageObj.rateUrself);
      props.setWasIBestCheck(localStorageObj.wasIBestCheck);
      props.setBookOrQuizCheck(localStorageObj.bookOrQuizCheck);
      props.setMtbCheck(localStorageObj.mtbCheck);
      props.setMusicCheck(localStorageObj.musicCheck);
      props.setWasYesterdayJealous(localStorageObj.wasYesterdayJealous);
    } else {
      props.setGratitudeList([{ value: "", label: "I'm Grateful for" }]);
      props.setMotherCare("");
      props.setTodayFeeling("");
      props.setMeditationCheck(false);
      props.setExerciseCheck(false);
      props.setYearGoalsList([
        { value: "", label: "By this year end, I'll achieve" },
        { value: "", label: "By this year end, I'll achieve" },
        { value: "", label: "By this year end, I'll achieve" },
        { value: "", label: "By this year end, I'll achieve" },
        { value: "", label: "By this year end, I'll achieve" },
      ]);
      props.setYearTopGoalsList([
        { value: "", label: "By this year end, I'll achieve" },
        { value: "", label: "By this year end, I'll achieve" },
        { value: "", label: "By this year end, I'll achieve" },
      ]);
      props.setMonthGoalsList([
        { value: "", label: "I'll achieve", group: 0 },
        { value: "", label: "I'll achieve", group: 0 },
        { value: "", label: "I'll achieve", group: 1 },
        { value: "", label: "I'll achieve", group: 1 },
        { value: "", label: "I'll achieve", group: 2 },
        { value: "", label: "I'll achieve", group: 2 },
      ]);
      props.setWeekGoalsList([
        { value: "", label: "I'll finish", group: 0 },
        { value: "", label: "I'll finish", group: 0 },
        { value: "", label: "I'll finish", group: 1 },
        { value: "", label: "I'll finish", group: 1 },
        { value: "", label: "I'll finish", group: 2 },
        { value: "", label: "I'll finish", group: 2 },
      ]);
      props.setTodoList([
        { value: "", label: "I'll do", group: 0 },
        { value: "", label: "I'll do", group: 0 },
        { value: "", label: "I'll do", group: 1 },
        { value: "", label: "I'll do", group: 1 },
        { value: "", label: "I'll do", group: 2 },
        { value: "", label: "I'll do", group: 2 },
      ]);
      props.setLegMassage(false);
      props.setMotherExercise(false);
      props.setSunScreenMorCheck(false);
      props.setSunScreenEveCheck(false);
      props.setRawVegIntakeCheck(false);
      props.setWaterCheck(false);
      props.setRetinaCheck(false);
      props.setSoakDhaniya(false);
      props.setNiceToday("");
      props.setRateUrself("");
      props.setWasIBestCheck(false);
      props.setBookOrQuizCheck(false);
      props.setMtbCheck(false);
      props.setMusicCheck(false);
      props.setWasYesterdayJealous(false);
    }

    setSelectedDate(date);
  };

  const toggleNavBarOpen = () => {
    setNavbarOpen(!navBarOpen);
  };

  return (
    <div>
      <AppBar>
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
            onClick={toggleNavBarOpen}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            Soldier: Calm is a Super Power
          </Typography>

          <IconButton
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            //onClick={handleMenu}
            color="inherit"
          >
            <SettingsIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      <Drawer
        open={navBarOpen}
        onClose={() => toggleNavBarOpen(false)}
        className={classes.navBar}
      >
        <DatePicker
          margin="normal"
          id="date-picker-dialog"
          variant="inline"
          format="DD/MM/YYYY"
          label="Choose Date"
          value={selectedDate}
          onChange={handleDateChange}
          KeyboardButtonProps={{
            "aria-label": "change date",
          }}
          className={classes.padding10}
        />
        <MenuItem onClick={() => props.history.push("/")}>Dashboard</MenuItem>
        <MenuItem onClick={() => props.history.push("/habit")}>
          Habit Tracker
        </MenuItem>
        <MenuItem onClick={() => props.history.push("/workground")}>
          Work Ground
        </MenuItem>
      </Drawer>
    </div>
  );
}
