import React from "react";

//Material UI Components
import {
  Grid,
  TextField,
  Switch,
  FormGroup,
  FormControlLabel,
  makeStyles,
} from "@material-ui/core";

//sub component
import Quotes from "./Quotes";

// Styles
const useStyles = makeStyles((theme) => ({
  marginTop15: {
    marginTop: "15px",
  },
}));

export default function EveningCheckList(props) {
  const classes = useStyles();

  const sunScreenEveCheck = props.sunScreenEveCheck,
    setSunScreenEveCheck = props.setSunScreenEveCheck,
    rawVegIntakeCheck = props.rawVegIntakeCheck,
    setRawVegIntakeCheck = props.setRawVegIntakeCheck,
    waterCheck = props.waterCheck,
    setWaterCheck = props.setWaterCheck;

  const quotesList = [
    "The goal of meditation isn't to control your thoughts, its to stop letting them control you",
    "All the problems so far faced is because of mind/thoughts, Take control over it",
    "Don't let your little thoughts stop your BIG dreams",
    "Meditate so that my mind cannot complicate my life",
    "Listen, Smile, Agree, and then work hard for whatever you want for",
    "The world is a mirror, smile and it will smile back",
    "Everyday is another oppurtunity to get closer to my goals",
    "Train your mind to be calm in every situation",
    "If there is no enemy within, the enemy outside can do us no harm",
    "Treat people/family members way you want to be treated",
    "I'm solely responsible for all the jou and sorrow in my life",
    "Before you speak, let your words pass through three gates: Is it True? Is it necessary? Is it kind?",
  ];

  return (
    <Grid container>
      <Grid item xs={12}>
        <Quotes quotesList={quotesList} />
      </Grid>
      <Grid item xs={12}>
        <FormGroup row className={classes.marginTop15}>
          <FormControlLabel
            control={
              <Switch
                checked={sunScreenEveCheck}
                onChange={(e) => setSunScreenEveCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Evening face sunscreen check"
          />
          <FormControlLabel
            control={
              <Switch
                checked={rawVegIntakeCheck}
                onChange={(e) => setRawVegIntakeCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Did we intake raw vegetables or fruits today with food?"
          />
          <FormControlLabel
            control={
              <Switch
                checked={waterCheck}
                onChange={(e) => setWaterCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Did I drink enough water today?"
          />
        </FormGroup>
      </Grid>
    </Grid>
  );
}
