import React from "react";

//Material UI Components
import {
  Grid,
  TextField,
  Button,
  makeStyles,
  InputAdornment,
  Typography,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";

//sub component
import Quotes from "./Quotes";

const useStyles = makeStyles((theme) => ({
  buttonHolder: {
    display: "flex",
    justifyContent: "flex-end",
    flexDirection: "row",
  },
  deletIcon: {
    cursor: "pointer",
  },
}));

export default function YearGoals(props) {
  const classes = useStyles();
  const yearGoalsList = props.yearGoalsList,
    setYearGoalsList = props.setYearGoalsList;

  const handleClick = () => {
    const list = [
      ...yearGoalsList,
      { value: "", label: "By this year end, I'll achieve" },
    ];
    setYearGoalsList(list);
  };

  const handleChange = (e) => {
    const id = e.target.id;
    const value = e.target.value;
    const list = [...yearGoalsList];
    list[id].value = value;
    setYearGoalsList(list);
  };

  const handleDelete = (index) => {
    const list = [...yearGoalsList];
    list.splice(index, 1);
    setYearGoalsList(list);
  };

  const quotesList = [
    "Risk more than others think is safe. Dream more than others think is practical",
    "Goal without a plan is just a Wish",
    "If plan doesn't work, change the plan, not the goal",
  ];

  return (
    <Grid container>
      <Grid item xs={8}>
        <Quotes quotesList={quotesList} />
      </Grid>
      <Grid item xs={4}>
        <div className={classes.buttonHolder}>
          <Button
            variant="contained"
            onClick={handleClick}
            startIcon={<AddIcon />}
            size="small"
          >
            Add
          </Button>
        </div>
      </Grid>
      {yearGoalsList && yearGoalsList.length > 0
        ? yearGoalsList.map((listItem, index) => {
            return (
              <Grid item xs={12}>
                <TextField
                  id={index}
                  label={listItem.label}
                  fullWidth
                  value={listItem.value}
                  onChange={handleChange}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment
                        position="end"
                        onClick={() => handleDelete(index)}
                        className={classes.deletIcon}
                      >
                        <DeleteIcon />
                      </InputAdornment>
                    ),
                  }}
                />
              </Grid>
            );
          })
        : null}
    </Grid>
  );
}
