import React, { useState } from "react";
import { Typography, makeStyles, Grid } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    paddingBottom: "20px",
  },
  years: {
    fontWeight: "bold",
    paddingRight: "2px",
  },
  seconds: {
    fontWeight: "bold",
    paddingTop: "20px",
  },
}));

export default function AgeIndicator(props) {
  const classes = useStyles();
  const [year, setYear] = useState(0);
  const [milliSecs, setMilliSecs] = useState(0);
  var dob = props.dob;
  var yearFormattedDob = new Date(dob).getFullYear();
  var millSecFormattedDob = Math.floor(new Date(dob).getTime() / 100);

  // Update the count down every 1 second
  var x = setInterval(function () {
    // Get today's date and time
    var now = new Date();
    var currentYear = now.getFullYear();
    var currentMillSeconds = Math.floor(now.getTime() / 100);

    // Time calculations for days, hours, minutes and seconds
    //var years = Math.floor(distance / (1000 * 60 * 60 * 24 * 365));
    var years = currentYear - yearFormattedDob;
    var millSeconds = currentMillSeconds - millSecFormattedDob;

    setYear(years);
    setMilliSecs(millSeconds);
    // Output the result in an element with id="demo"

    // document.getElementById("years").innerHTML = years;
    // document.getElementById("milliSeconds").innerHTML = "." + millSeconds;
  }, 1);

  return (
    <Grid container justify="center" spacing={2} className={classes.root}>
      <Typography
        id="years"
        variant="h1"
        align="center"
        display="inline"
        className={classes.years}
      >
        {year}
      </Typography>
      <Typography
        id="milliSeconds"
        variant="h4"
        align="center"
        display="inline"
        className={classes.seconds}
      >{`.${milliSecs}`}</Typography>
    </Grid>
  );
}
