import React from "react";

//Material UI Components
import {
  Grid,
  TextField,
  Button,
  makeStyles,
  InputAdornment,
  Typography,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";

//sub component
import Quotes from "./Quotes";

const useStyles = makeStyles((theme) => ({
  buttonHolder: {
    display: "flex",
    justifyContent: "flex-end",
    flexDirection: "row",
  },
  deletIcon: {
    cursor: "pointer",
  },
}));

export default function GratitudeList(props) {
  // const [gratitudeList, setGratitudeList] = useState([
  //   { value: "", label: "I'm Grateful for" },
  // ]);
  const classes = useStyles();
  const gratitudeList = props.gratitudeList,
    setGratitudeList = props.setGratitudeList;

  const handleClick = () => {
    const list = [...gratitudeList, { value: "", label: "I'm Grateful for" }];
    setGratitudeList(list);
  };

  const handleChange = (e) => {
    const id = e.target.id;
    const value = e.target.value;
    const list = [...gratitudeList];
    list[id].value = value;
    setGratitudeList(list);
  };

  const handleDelete = (index) => {
    const list = [...gratitudeList];
    list.splice(index, 1);
    setGratitudeList(list);
  };

  const quotesList = [
    "Remember you've born for a purpose which is to serve your Mother",
    "Only truth in this world is Parents (Mother)",
    "Family always comes first",
    "Never sacrifice your family. They are the most important thing in life.",
    "Start each day with a Positive thought and a grateful heart.",
  ];

  return (
    <Grid container>
      <Grid item xs={8}>
        <Quotes quotesList={quotesList} />
      </Grid>
      <Grid item xs={4}>
        <div className={classes.buttonHolder}>
          <Button
            variant="contained"
            onClick={handleClick}
            startIcon={<AddIcon />}
            size="small"
          >
            Add
          </Button>
        </div>
      </Grid>
      {gratitudeList && gratitudeList.length > 0
        ? gratitudeList.map((listItem, index) => {
            return (
              <Grid item xs={12}>
                <TextField
                  id={index}
                  label={listItem.label}
                  value={listItem.value}
                  fullWidth
                  onChange={handleChange}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment
                        position="end"
                        onClick={() => handleDelete(index)}
                        className={classes.deletIcon}
                      >
                        <DeleteIcon />
                      </InputAdornment>
                    ),
                  }}
                />
              </Grid>
            );
          })
        : null}
    </Grid>
  );
}
