import React from "react";

//Material UI Components
import {
  Grid,
  TextField,
  Button,
  makeStyles,
  InputAdornment,
  Typography,
} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";

//sub component
import Quotes from "./Quotes";

const useStyles = makeStyles((theme) => ({
  buttonHolder: {
    display: "flex",
    justifyContent: "flex-end",
    flexDirection: "row",
  },
  deletIcon: {
    cursor: "pointer",
  },
}));

export default function TopYearGoals(props) {
  const yearGoalsList = props.yearGoalsList,
    yearTopGoalsList = props.yearTopGoalsList,
    setYearTopGoalsList = props.setYearTopGoalsList;
  const classes = useStyles();

  const handleChange = (e, selectedItem, index) => {
    const list = [...yearTopGoalsList];
    list[index].value = selectedItem.value;
    setYearTopGoalsList(list);
  };

  const quotesList = [    
    "A Fool with a plan is better than a genius without one",
    "Planning is a skill and art of journey which needs regular optimizations to progress and keep you in track",
  ];

  return (
    <Grid container>
      <Grid item xs={12}>
        <Quotes quotesList={quotesList} />
      </Grid>

      {yearTopGoalsList && yearTopGoalsList.length > 0
        ? yearTopGoalsList.map((listItem, index) => {
            return (
              <Grid item xs={12}>
                <Autocomplete
                  id="combo-box-demo"
                  options={yearGoalsList}
                  getOptionLabel={(option) => option.value}
                  value={listItem}
                  onChange={(e, selectedItem) =>
                    handleChange(e, selectedItem, index)
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="By this year end, No matter what - I'll achieve"
                      fullWidth
                    />
                  )}
                />
              </Grid>
            );
          })
        : null}
    </Grid>
  );
}
