const today = moment();

// Styles
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginTop: "20px",
  },
  paper: {
    height: 140,
    width: 100,
  },
  control: {
    padding: theme.spacing(2),
  },
  paddingTop30: {
    paddingTop: "30px",
  },
  dateHolder: {
    marginTop: "85px",
    background: "#00e5ff",
    padding: "4px 15px",
    borderRadius: "10px 10px 0 0",
    fontSize: "13px",
    fontWeight: "500",
  },
  mainContainer: {
    padding: "24px",
    backgroundColor: "#f5f5f5",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightBold,
  },
  title: {
    flexGrow: 1,
  },
  navBar: {
    "& li": {
      width: "250px",
    },
  },
  saveButtonHolder: {
    display: "flex",
    justifyContent: "flex-end",
    flexDirection: "row",
    marginTop: "20px",
  },
}));

export default function App(props) {
  const [expanded, setExpanded] = React.useState(false);
  //const [expanded, setExpanded] = React.useState("panel8");
  const [selectedDate, setSelectedDate] = React.useState(today);
  const [cycle, setCycle] = useState(0);

  //Sub Components Lists
  const [gratitudeList, setGratitudeList] = useState([
    { value: "", label: "I'm Grateful for" },
  ]);
  const [motherCare, setMotherCare] = useState("");
  const [todayFeeling, setTodayFeeling] = useState("");
  const [meditationCheck, setMeditationCheck] = useState(false);
  const [sunScreenMorCheck, setSunScreenMorCheck] = useState(false);
  const [exerciseCheck, setExerciseCheck] = useState(false);
  const [yearGoalsList, setYearGoalsList] = useState([
    { value: "", label: "By this year end, I'll achieve" },
    { value: "", label: "By this year end, I'll achieve" },
    { value: "", label: "By this year end, I'll achieve" },
    { value: "", label: "By this year end, I'll achieve" },
    { value: "", label: "By this year end, I'll achieve" },
  ]);
  const [yearTopGoalsList, setYearTopGoalsList] = useState([
    { value: "", label: "By this year end, I'll achieve" },
    { value: "", label: "By this year end, I'll achieve" },
    { value: "", label: "By this year end, I'll achieve" },
  ]);
  const [monthGoalsList, setMonthGoalsList] = useState([
    { value: "", label: "I'll achieve", group: 0 },
    { value: "", label: "I'll achieve", group: 0 },
    { value: "", label: "I'll achieve", group: 1 },
    { value: "", label: "I'll achieve", group: 1 },
    { value: "", label: "I'll achieve", group: 2 },
    { value: "", label: "I'll achieve", group: 2 },
  ]);
  const [weekGoalsList, setWeekGoalsList] = useState([
    { value: "", label: "I'll finish", group: 0 },
    { value: "", label: "I'll finish", group: 0 },
    { value: "", label: "I'll finish", group: 1 },
    { value: "", label: "I'll finish", group: 1 },
    { value: "", label: "I'll finish", group: 2 },
    { value: "", label: "I'll finish", group: 2 },
  ]);
  const [todoList, setTodoList] = useState([
    { value: "", label: "I'll do", group: 0 },
    { value: "", label: "I'll do", group: 0 },
    { value: "", label: "I'll do", group: 1 },
    { value: "", label: "I'll do", group: 1 },
    { value: "", label: "I'll do", group: 2 },
    { value: "", label: "I'll do", group: 2 },
  ]);
  const [sunScreenEveCheck, setSunScreenEveCheck] = useState(false);
  const [rawVegIntakeCheck, setRawVegIntakeCheck] = useState(false);
  const [waterCheck, setWaterCheck] = useState(false);
  const [legMassage, setLegMassage] = useState(false);
  const [motherExercise, setMotherExercise] = useState(false);
  const [soakDhaniya, setSoakDhaniya] = useState(false);
  const [retinaCheck, setRetinaCheck] = useState(false);
  const [niceToday, setNiceToday] = useState("");
  const [rateUrself, setRateUrself] = useState("");
  const [wasIBestCheck, setWasIBestCheck] = useState(false);
  const [bookOrQuizCheck, setBookOrQuizCheck] = useState(false);
  const [mtbCheck, setMtbCheck] = useState(false);
  const [musicCheck, setMusicCheck] = useState(false);
  const [wasYesterdayJealous, setWasYesterdayJealous] = useState(false);

  useEffect(() => {
    //load values from local storaqge
    const localStorageObj = JSON.parse(
      localStorage.getItem(today.format("DDMMYYYY"))
    );

    if (localStorageObj) {
      setGratitudeList(localStorageObj.gratitudeList);
      setMotherCare(localStorageObj.motherCare);
      setTodayFeeling(localStorageObj.todayFeeling);
      setMeditationCheck(localStorageObj.meditationCheck);
      setExerciseCheck(localStorageObj.exerciseCheck);
      setYearGoalsList(localStorageObj.yearGoalsList);
      setYearTopGoalsList(localStorageObj.yearTopGoalsList);
      setMonthGoalsList(localStorageObj.monthGoalsList);
      setWeekGoalsList(localStorageObj.weekGoalsList);
      setTodoList(localStorageObj.todoList);
      setLegMassage(localStorageObj.legMassage);
      setMotherExercise(localStorageObj.motherExercise);
      setSunScreenMorCheck(localStorageObj.sunScreenMorCheck);
      setSunScreenEveCheck(localStorageObj.sunScreenEveCheck);
      setRawVegIntakeCheck(localStorageObj.rawVegIntakeCheck);
      setWaterCheck(localStorageObj.waterCheck);
      setRetinaCheck(localStorageObj.retinaCheck);
      setSoakDhaniya(localStorageObj.soakDhaniya);
      setNiceToday(localStorageObj.niceToday);
      setRateUrself(localStorageObj.rateUrself);
      setWasIBestCheck(localStorageObj.wasIBestCheck);
      setBookOrQuizCheck(localStorageObj.bookOrQuizCheck);
      setMtbCheck(localStorageObj.mtbCheck);
      setMusicCheck(localStorageObj.musicCheck);
      setWasYesterdayJealous(localStorageObj.wasYesterdayJealous);
    }

    //Keep System Awake all the time
    // The wake lock sentinel.
    let wakeLock = null;

    // Function that attempts to request a screen wake lock.
    const requestWakeLock = async () => {
      try {
        wakeLock = await navigator.wakeLock.request("screen");
        wakeLock.addEventListener("release", () => {
          console.log("Soldier Screen Wake Lock was released");
        });
        console.log("Soldier Screen Wake Lock is active");
      } catch (err) {
        console.error(`Soldier ${err.name}, ${err.message}`);
      }
    };

    // Request a screen wake lock
    requestWakeLock();

    // Reacquire the screen wake lock,
    const handleVisibilityChange = () => {
      if (wakeLock !== null && document.visibilityState === "visible") {
        console.log("Soldier reacquire the screen wake lock");
        requestWakeLock();
      }
    };

    document.addEventListener("visibilitychange", handleVisibilityChange);

    return () => {
      wakeLock.release();
    };
  }, []);

  const classes = useStyles();
  const dob = "1961-05-16";

  const handleAccordionChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  const HandleSave = () => {
    let saveObj = {
      gratitudeList,
      motherCare,
      todayFeeling,
      meditationCheck,
      sunScreenMorCheck,
      exerciseCheck,
      yearGoalsList,
      yearTopGoalsList,
      monthGoalsList,
      weekGoalsList,
      todoList,
      legMassage,
      motherExercise,
      sunScreenEveCheck,
      rawVegIntakeCheck,
      waterCheck,
      retinaCheck,
      soakDhaniya,
      niceToday,
      rateUrself,
      wasIBestCheck,
      bookOrQuizCheck,
      mtbCheck,
      musicCheck,
      wasYesterdayJealous,
    };
    localStorage.setItem(
      selectedDate.format("DDMMYYYY"),
      JSON.stringify(saveObj)
    );
  };

  const quotesList = [
    "Your problem isn't the problem, your reaction is the problem",
    "Keep Calm and Be the Fighter",
    "Ego/Jealous - Bending down is becoming tough - then remember a day/job in microcristal",
    "If your mind/ situation says - Mother is wrong. Remember when you lost Father, only thing learnt was to blindly listen Mother",
    "Everyday/ hour/ minute is minuscule of the whole life you want was a gift by your parents(never hurt them), plan & play accordingly",
    "Pain will leave, once it has finished teaching you",
    "Attack your Goals like your life depends on it",
    "Excellence happens not by accident. It is a process.",
    "Warriors are not the ones who always win, but the ones that always fight",
    "Our greatest weakness lies in giving up. The most certain way to succeed is always to try just one more time",
  ];

  return (
    <div className={classes.root}>
      <Header
        history={props.history}
        selectedDate={selectedDate}
        setSelectedDate={setSelectedDate}
        setGratitudeList={setGratitudeList}
        setMotherCare={setMotherCare}
        setSunScreenMorCheck={setSunScreenMorCheck}
        setTodayFeeling={setTodayFeeling}
        setMeditationCheck={setMeditationCheck}
        setExerciseCheck={setExerciseCheck}
        setYearGoalsList={setYearGoalsList}
        setYearTopGoalsList={setYearTopGoalsList}
        setMonthGoalsList={setMonthGoalsList}
        setWeekGoalsList={setWeekGoalsList}
        setTodoList={setTodoList}
        setLegMassage={setLegMassage}
        setMotherExercise={setMotherExercise}
        setRetinaCheck={setRetinaCheck}
        setSoakDhaniya={setSoakDhaniya}
        setNiceToday={setNiceToday}
        setRateUrself={setRateUrself}
        setWasIBestCheck={setWasIBestCheck}
        setBookOrQuizCheck={setBookOrQuizCheck}
        setMtbCheck={setMtbCheck}
        setMusicCheck={setMusicCheck}
        setWasYesterdayJealous={setWasYesterdayJealous}
        setSunScreenEveCheck={setSunScreenEveCheck}
        setRawVegIntakeCheck={setRawVegIntakeCheck}
        setWaterCheck={setWaterCheck}
      />

      <Grid container justify="center">
        <Grid item>
          <Typography className={classes.dateHolder}>
            {selectedDate.format("MMMM Do YYYY")}
          </Typography>
        </Grid>
      </Grid>
      <Container maxWidth="lg" className={classes.mainContainer}>
        <Quotes quotesList={quotesList} mainPage={true} />
        <AgeIndicator dob={dob} />
        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel1"}
          headLine={"Gratitude List"}
        >
          <GratitudeList
            gratitudeList={gratitudeList}
            setGratitudeList={setGratitudeList}
          />
        </AccordionItem>

        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel2"}
          headLine={"Morning CheckList"}
        >
          <MorningCheckList
            motherCare={motherCare}
            setMotherCare={setMotherCare}
            sunScreenMorCheck={sunScreenMorCheck}
            setSunScreenMorCheck={setSunScreenMorCheck}
            todayFeeling={todayFeeling}
            setTodayFeeling={setTodayFeeling}
            meditationCheck={meditationCheck}
            setMeditationCheck={setMeditationCheck}
            exerciseCheck={exerciseCheck}
            setExerciseCheck={setExerciseCheck}
          />
        </AccordionItem>

        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel3"}
          headLine={"Year End Goals"}
        >
          <YearGoals
            yearGoalsList={yearGoalsList}
            setYearGoalsList={setYearGoalsList}
          />
        </AccordionItem>

        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel4"}
          headLine={"Prioritize: Top 3 Goals"}
        >
          <TopYearGoals
            yearGoalsList={yearGoalsList}
            yearTopGoalsList={yearTopGoalsList}
            setYearTopGoalsList={setYearTopGoalsList}
          />
        </AccordionItem>

        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel5"}
          headLine={"Month End Goals"}
        >
          <MonthEndGoals
            yearTopGoalsList={yearTopGoalsList}
            monthGoalsList={monthGoalsList}
            setMonthGoalsList={setMonthGoalsList}
          />
        </AccordionItem>

        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel6"}
          headLine={"Week End Goals"}
        >
          <WeekEndGoals
            yearTopGoalsList={yearTopGoalsList}
            weekGoalsList={weekGoalsList}
            setWeekGoalsList={setWeekGoalsList}
          />
        </AccordionItem>

        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel7"}
          headLine={"Today - To Do List"}
        >
          <ToDoList
            yearTopGoalsList={yearTopGoalsList}
            todoList={todoList}
            setTodoList={setTodoList}
          />
        </AccordionItem>

        {/* <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel8"}
          headLine={`Pomodoro Timer (${cycle})`}
        >
          <PomodoroTimer cycle={cycle} setCycle={setCycle} />
        </AccordionItem> */}

        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel8"}
          headLine={"Evening CheckList"}
        >
          <EveningCheckList
            sunScreenEveCheck={sunScreenEveCheck}
            setSunScreenEveCheck={setSunScreenEveCheck}
            rawVegIntakeCheck={rawVegIntakeCheck}
            setRawVegIntakeCheck={setRawVegIntakeCheck}
            waterCheck={waterCheck}
            setWaterCheck={setWaterCheck}
          />
        </AccordionItem>

        <AccordionItem
          expanded={expanded}
          handleAccordionChange={handleAccordionChange}
          panelId={"panel9"}
          headLine={"Night CheckList"}
        >
          <NightCheckList
            legMassage={legMassage}
            setLegMassage={setLegMassage}
            motherExercise={motherExercise}
            setMotherExercise={setMotherExercise}
            retinaCheck={retinaCheck}
            setRetinaCheck={setRetinaCheck}
            soakDhaniya={soakDhaniya}
            setSoakDhaniya={setSoakDhaniya}
            niceToday={niceToday}
            setNiceToday={setNiceToday}
            rateUrself={rateUrself}
            setRateUrself={setRateUrself}
            wasIBestCheck={wasIBestCheck}
            setWasIBestCheck={setWasIBestCheck}
            bookOrQuizCheck={bookOrQuizCheck}
            setBookOrQuizCheck={setBookOrQuizCheck}
            mtbCheck={mtbCheck}
            setMtbCheck={setMtbCheck}
            musicCheck={musicCheck}
            setMusicCheck={setMusicCheck}
            wasYesterdayJealous={wasYesterdayJealous}
            setWasYesterdayJealous={setWasYesterdayJealous}
          />
        </AccordionItem>
        <div className={classes.saveButtonHolder}>
          <Button
            variant="contained"
            color="primary"
            size="large"
            startIcon={<SaveIcon />}
            onClick={HandleSave}
          >
            Save The Plan
          </Button>
        </div>
      </Container>
    </div>
  );
}
