import React from "react";

//Material UI Components
import {
  Grid,
  TextField,
  Switch,
  FormGroup,
  FormControlLabel,
  makeStyles,
} from "@material-ui/core";

//sub component
import Quotes from "./Quotes";

// Styles
const useStyles = makeStyles((theme) => ({
  marginTop15: {
    marginTop: "15px",
  },
}));

export default function MorningCheckList(props) {
  const classes = useStyles();

  const motherCare = props.motherCare,
    setMotherCare = props.setMotherCare,
    todayFeeling = props.todayFeeling,
    setTodayFeeling = props.setTodayFeeling,
    meditationCheck = props.meditationCheck,
    setMeditationCheck = props.setMeditationCheck,
    exerciseCheck = props.exerciseCheck,
    setExerciseCheck = props.setExerciseCheck,
    setSunScreenMorCheck = props.setSunScreenMorCheck,
    sunScreenMorCheck = props.sunScreenMorCheck;

  const quotesList = [
    "The goal of meditation isn't to control your thoughts, its to stop letting them control you",
    "All the problems so far faced is because of mind/thoughts, Take control over it",
    "Don't let your little thoughts stop your BIG dreams",
    "Meditate so that my mind cannot complicate my life",
    "Listen, Smile, Agree, and then work hard for whatever you want for",
    "The world is a mirror, smile and it will smile back",
    "Everyday is another oppurtunity to get closer to my goals",
    "Train your mind to be calm in every situation",
    "If there is no enemy within, the enemy outside can do us no harm",
    "Treat people/family members way you want to be treated",
    "I'm solely responsible for all the jou and sorrow in my life",
    "Before you speak, let your words pass through three gates: Is it True? Is it necessary? Is it kind?",
  ];

  return (
    <Grid container>
      <Grid item xs={12}>
        <Quotes quotesList={quotesList} />
      </Grid>
      <Grid item xs={12}>
        <TextField
          id="standard4"
          label="Will I Take Good Care of Mother? "
          value={motherCare}
          onChange={(e) => setMotherCare(e.target.value)}
          //placeholder="Mother, Family, Sister, Niece "
          fullWidth
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          id="standard5"
          label="How do I want to feel today?"
          placeholder="Joyous, Calm, Energetic, Caring, etc "
          value={todayFeeling}
          onChange={(e) => setTodayFeeling(e.target.value)}
          fullWidth
        />
      </Grid>
      <Grid item xs={12}>
        <FormGroup row className={classes.marginTop15}>
          <FormControlLabel
            control={
              <Switch
                checked={meditationCheck}
                onChange={(e) => setMeditationCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Meditation Check (Mind)"
          />
          <FormControlLabel
            control={
              <Switch
                checked={exerciseCheck}
                onChange={(e) => setExerciseCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Exercise Check (Body)"
          />
          <FormControlLabel
            control={
              <Switch
                checked={sunScreenMorCheck}
                onChange={(e) => setSunScreenMorCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Face sunscreen & body lotion check"
          />
        </FormGroup>
      </Grid>
    </Grid>
  );
}
