import React from "react";

//Material UI Components
import {
  Grid,
  TextField,
  Switch,
  FormGroup,
  FormControlLabel,
  makeStyles,
} from "@material-ui/core";

//sub component
import Quotes from "./Quotes";

// Styles
const useStyles = makeStyles((theme) => ({
  marginTop15: {
    marginTop: "15px",
  },
}));

export default function NightCheckList(props) {
  const legMassage = props.legMassage,
    setLegMassage = props.setLegMassage,
    motherExercise = props.motherExercise,
    setMotherExercise = props.setMotherExercise,
    soakDhaniya = props.soakDhaniya,
    setSoakDhaniya = props.setSoakDhaniya,
    retinaCheck = props.retinaCheck,
    setRetinaCheck = props.setRetinaCheck,
    niceToday = props.niceToday,
    setNiceToday = props.setNiceToday,
    rateUrself = props.rateUrself,
    setRateUrself = props.setRateUrself,
    wasIBestCheck = props.wasIBestCheck,
    setWasIBestCheck = props.setWasIBestCheck,
    bookOrQuizCheck = props.bookOrQuizCheck,
    setBookOrQuizCheck = props.setBookOrQuizCheck,
    mtbCheck = props.mtbCheck,
    setMtbCheck = props.setMtbCheck,
    musicCheck = props.musicCheck,
    setMusicCheck = props.setMusicCheck,
    wasYesterdayJealous = props.wasYesterdayJealous,
    setWasYesterdayJealous = props.setWasYesterdayJealous;

  const classes = useStyles();

  const quotesList = [
    "Happiness/Achievement/Pride, when you defect the shittier version of yourself",
    "If there is no enemy within, the enemy outside can do us no harm",
  ];

  return (
    <Grid container>
      <Grid item xs={12}>
        <Quotes quotesList={quotesList} />
      </Grid>
      <Grid item xs={12}>
        <FormGroup row className={classes.marginTop15}>
          <FormControlLabel
            control={
              <Switch
                checked={legMassage}
                onChange={(e) => setLegMassage(e.target.checked)}
                color="primary"
              />
            }
            label="Leg Massage for Mother?"
          />
          <FormControlLabel
            control={
              <Switch
                checked={motherExercise}
                onChange={(e) => setMotherExercise(e.target.checked)}
                color="primary"
              />
            }
            label="Did Mother do Exercise Today?"
          />
          <FormControlLabel
            control={
              <Switch
                checked={soakDhaniya}
                onChange={(e) => setSoakDhaniya(e.target.checked)}
                color="primary"
              />
            }
            label="Soak Dhaniya in water overnight?"
          />
          <FormControlLabel
            control={
              <Switch
                checked={retinaCheck}
                onChange={(e) => setRetinaCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Did I apply Retina cream and moisturizer before sleep?"
          />
        </FormGroup>
      </Grid>
      <Grid item xs={12}>
        <TextField
          id="standard4"
          label="To whom and all, was I nice today?"
          value={niceToday}
          onChange={(e) => setNiceToday(e.target.value)}
          fullWidth
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          id="standard5"
          label="Rate yourself, out of 10?"
          value={rateUrself}
          onChange={(e) => setRateUrself(e.target.value)}
          fullWidth
        />
      </Grid>
      <Grid item xs={12}>
        <FormGroup row className={classes.marginTop15}>
          <FormControlLabel
            control={
              <Switch
                checked={wasIBestCheck}
                onChange={(e) => setWasIBestCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Was this my Best?"
          />
          <FormControlLabel
            control={
              <Switch
                checked={wasYesterdayJealous}
                onChange={(e) => setWasYesterdayJealous(e.target.checked)}
                color="primary"
              />
            }
            label="Atleast, Was my yesterday jealous of my today?"
          />
          <FormControlLabel
            control={
              <Switch
                checked={bookOrQuizCheck}
                onChange={(e) => setBookOrQuizCheck(e.target.checked)}
                color="primary"
              />
            }
            label="Read a book or JavaScript Quiz?"
          />
          <FormControlLabel
            control={
              <Switch
                checked={mtbCheck}
                onChange={(e) => setMtbCheck(e.target.checked)}
                color="primary"
              />
            }
            label="I didn't mCode today?"
          />
          <FormControlLabel
            control={
              <Switch
                checked={musicCheck}
                onChange={(e) => setMusicCheck(e.target.checked)}
                color="primary"
              />
            }
            label="In sleep, Listen to audio version of yourself, you want to be?"
          />
        </FormGroup>
      </Grid>
    </Grid>
  );
}
