import React from "react";

//Material UI Components
import {
  Grid,
  TextField,
  Typography,
  makeStyles,
  InputAdornment,
  Button,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";

//sub component
import Quotes from "./Quotes";

// Styles
const useStyles = makeStyles((theme) => ({
  buttonHolder: {
    display: "flex",
    justifyContent: "flex-end",
    flexDirection: "row",
  },
  deletIcon: {
    cursor: "pointer",
  },
  subHeading: {
    fontWeight: "bold",
  },
}));

export default function MonthEndGoals(props) {
  const yearTopGoalsList = props.yearTopGoalsList,
    monthGoalsList = props.monthGoalsList,
    setMonthGoalsList = props.setMonthGoalsList;

  const classes = useStyles();

  const handleClick = (index) => {
    const list = [
      ...monthGoalsList,
      { value: "", label: "I'll achieve", group: index },
    ];
    setMonthGoalsList(list);
  };

  const handleChange = (e) => {
    const id = e.target.id;
    const value = e.target.value;
    const list = [...monthGoalsList];
    list[id].value = value;
    setMonthGoalsList(list);
  };

  const handleDelete = (idx) => {
    const list = [...monthGoalsList];
    list.splice(idx, 1);
    setMonthGoalsList(list);
  };

  const quotesList = [
    "Never giveup on something you can't go a day without thinking about",
    "If its is Important to you, You will find a way. If not, you'll find an Excuse",
    "You always have time for the things you put first",
    "There are no Problems, its always a challenges",
  ];

  return (
    <Grid container spacing={2}>
      <Grid item xs={12}>
        <Quotes quotesList={quotesList} />
      </Grid>
      {yearTopGoalsList && yearTopGoalsList.length > 0
        ? yearTopGoalsList.map((categoryListItem, index) => {
            return (
              <Grid item xs={12} md={4}>
                <Grid container>
                  <Grid item xs={8}>
                    <Typography className={classes.subHeading}>
                      {categoryListItem.value}
                    </Typography>
                  </Grid>

                  <Grid item xs={4}>
                    <div className={classes.buttonHolder}>
                      <Button
                        variant="contained"
                        onClick={() => handleClick(index)}
                        startIcon={<AddIcon />}
                        size="small"
                      >
                        Add
                      </Button>
                    </div>
                  </Grid>
                  {monthGoalsList && monthGoalsList.length > 0
                    ? monthGoalsList.map((listItem, idx) => {
                        if (listItem.group === index) {
                          return (
                            <TextField
                              id={idx}
                              label={listItem.label}
                              value={listItem.value}
                              fullWidth
                              onChange={handleChange}
                              InputProps={{
                                endAdornment: (
                                  <InputAdornment
                                    position="end"
                                    onClick={() => handleDelete(idx)}
                                    className={classes.deletIcon}
                                  >
                                    <DeleteIcon />
                                  </InputAdornment>
                                ),
                              }}
                            />
                          );
                        }
                      })
                    : null}
                </Grid>
              </Grid>
            );
          })
        : null}
    </Grid>
  );
}
